import React from 'react'
import { Grid } from 'react-bootstrap'

const NotFoundPage = () => (
  <section className='section--white'>
    <Grid>
      <h1>NOT FOUND</h1>
      <p>You just hit a route that doesn&#39;t exist... the sadness.</p>
    </Grid>
  </section>
)

export default NotFoundPage



